<template>
  <div class="getDriveCardByInput">
    <!-- <input type="password" autofocus> -->
    <!-- 顶部小的提示信息 -->
    <div class="topTip">
      <span class="iconfont icon-anquan1" style="font-size:12px; "></span>
      以下信息仅供交管局查询使用，我们将严格保密
    </div>

    <div class="ybox">
      <van-cell class="carNum" style="border:none;">
        <span>车牌号码</span>
        <span class="photograph" style="display:flex; algin-item:center; ">
          <span class="iconfont icon-xiangji" style="margin-right:2px;"></span>拍照识别
        </span>
      </van-cell>

      <!-- 输入框 -->
      <van-cell class="inputBox">
        <Input ref="inputFlag" />
      </van-cell>

      <!-- 下面的车辆代码 -->
      <van-cell class="carCode" style="overflow:hidden;">
        <template slot="title">
          <span class="van-cell-text">车辆识别代码</span>
          <span class="iconfont icon-icon" @click="$refs.xsz.show = true"></span>
        </template>
        <van-field
          @focus="focusCarNum"
          input-align="right"
          @input="changeToBigWithCarNum"
          v-model="carNum"
          :placeholder="isShowFullTip==false?'车辆识别代码后6位':'完整的车辆识别代码'"
        />
      </van-cell>

      <!--发动机号码  -->
      <van-cell class="carCode" style="overflow:hidden;">
        <template slot="title">
          <span class="van-cell-text">发动机号</span>
          <span class="iconfont icon-icon" @click="$refs.fdj.show = true"></span>
        </template>
        <van-field
          @input="changeToBigWithFdjNum"
          @focus="focusCarNum"
          v-model="fdjNum"
          :placeholder="isShowFullTip==false?'发动机号后6位':'完整的发动机号'"
        />
      </van-cell>
    </div>

    <div class="ybox">
      <van-cell title="车辆类型" @click="selectCarType()" is-link value="选择车辆类型" />
      <van-cell title="车辆性质" @click="selectCarNature()" is-link value="选择车辆性质" />
      <van-cell title="数据来源" class="dataFrom" @click="selectData()" is-link>
        <template slot="right-icon">
          <!-- 只有在全国一线的时候才显示推荐按钮 -->
          <span class="suggestLine">推荐</span>
          <span class="van-cell-text" style="color:#333; font-size:16px;">
            {{dataFrom}}
            <span class="icon-right iconfont" style="color:#999"></span>
          </span>
        </template>
      </van-cell>
    </div>

    <div class="ybox">
      <!-- <van-field name="switch" label="超证检测" class="switchW">
        <template #input>
          <van-switch v-model="isQueryCard" size="20" class="switchItem" />
        </template>
      </van-field>-->
      <van-field name="switch" label="查历史违章" class="switchW">
        <template #input>
          <van-switch v-model="isQueryHistory" size="20" class="switchItem" />
        </template>
      </van-field>
    </div>

    <div class="submitBtn">
      <!-- <van-button type="default" >保存并查询</van-button> -->
      <van-button type="default" :class="{'btnDisabled':isQuery == false}">
        <span v-if="isQueryHistory">查询历史违章</span>
        <!-- <span v-else-if="isQueryCard">违章查询+超证检测</span> -->
        <span v-else>查询</span>
      </van-button>
    </div>
    <!-- <div class="carList" @click="selectCarList">车辆列表</div> -->

    <!-- 选择数据来源 -->
    <yPopUp ref="dataFrom" :title="'请选择线路'">
      <div class="dataFromPop">
        <div class="dataItem dataActive">
          全国一线
          <span class="iconfont mark icon-xuanzhong"></span>
        </div>
        <div class="dataItem">全国一线</div>
        <div class="dataItem">全国一线</div>
        <div class="dataItem">广东省</div>
        <div class="dataItem">全国一线</div>
      </div>
    </yPopUp>

    <!-- 车辆性质 -->
    <!-- 如果不传递title 那么默认没有头部 -->
    <yPopUp ref="carProps" :title="'选择车辆性质'">
      <div class="carType">
        <div
          v-for="(item,idx) in carPropsList"
          @click="carProps = idx+1"
          :class="{'typeItem':true,'carPropsChecked':idx+1 == carProps}"
          :key="idx"
        >
          <span class="iconfont icon-xuanzhong mark" v-if="idx+1 == carProps"></span>
          <img src="/images/sjc.png" alt />
          <span class="rightTitle">{{item.name}}</span>
        </div>
      </div>
    </yPopUp>

    <!-- 选择车辆列表 -->
    <!-- <yPopUp ref="carList" :title="'切换车辆'"> 
      <div class="carListPop">
        <div class="typeItem" @click="carListActive = item" v-for = "item in 3" :key = "item"  :class="{'carListActive':carListActive==item}">
          <span v-if="carListActive == item" class="iconfont icon-xuanzhong mark "></span>
          <img src="/images/carList.png" alt />
          <span class="rightTitle">粤A&nbsp;64R22<br> 
              <span class="rightType">小型轿车</span>
          </span>
        </div>
      </div>
    </yPopUp>-->

    <!-- 车辆类型 -->
    <yPopUp ref="carType">
      <div class="carTypePop">
        <van-picker
          title="选择车辆类型"
          show-toolbar
          :columns="carTypes"
          @confirm="onConfirm"
          @cancel="onCancel"
          @change="onChange"
        />
      </div>
    </yPopUp>

    <dialogAlert ref="xsz">
      <span class="xsz">
        <img src="/images/xsz.png" alt />
      </span>
    </dialogAlert>

    <dialogAlert ref="fdj">
      <span class="xsz">
        <img src="/images/fdj.png" alt />
      </span>
    </dialogAlert>
  </div>
</template>
<script>
import Input from "../components/common/Input.vue";
import keyboard from "../components/common/keyboard.vue";
import { Toast } from "vant";
// import popUp from '../components/common/popUp.vue';
export default {
  data() {
    return {
      carListActive: 1, //车辆列表中选中的车辆
      carTypes: ["小型车", "紧凑型车", "中型车", "中大型车", "大型车"], //车辆类型
      isQueryHistory: false, //是否查询历史违章
      // isQueryCard:false,//是否检测超正
      dataFrom: "1",
      // 如果车牌号首位是“京”或“沪”，则车架号和发动机号的提示文字变更为“完整的车辆识别代号”
      isShowFullTip: false, //
      fdjNum: "", //发动机号码
      carNum: "", //车辆唯一识别代码
      carType: "", //车辆类型
      carProps: "", //车辆性质
      carPropsList:[
         "死角侧",
         "死角侧1",
         "死角侧3",
         "死角侧4",
         "死角侧5",
         "死角侧6",
      ],
      isQuery: false //是不是已经可以点击查询了（根据填写的信息是不是符合条件）
    };
  },
  mounted() {
    //this.$refs.keyboard.show = true;
  },
  components: {
    Input,
    keyboard
    //  yPopUp
  },
  methods: {
    onConfirm(value, index) {
      // Toast(`当前值：${value}, 当前索引：${index}`);
      this.$refs["carType"].show = false;
    },
    onChange(picker, value, index) {
      // Toast(`当前值：${value}, 当前索引：${index}`);
      this.carType = value;
    },
    onCancel() {
      // Toast('取消');
      this.$refs["carType"].show = false;
    },
    //选择车辆类型
    selectCarType() {
      this.$refs["carType"].show = true;
      this.focusCarNum();
    },
    // 选择车辆性质
    selectCarNature() {
      this.$refs["carProps"].show = true;
      this.focusCarNum();
    },
    //切换车辆
    selectCarList() {
      this.$refs.carList.show = true;
      this.focusCarNum();
    },
    //选择数据来源
    selectData() {
      this.$refs["dataFrom"].show = true;
      this.focusCarNum();
    },
    //关闭所有键盘
    focusCarNum() {
      console.log("输入车辆信息");
      this.$refs.inputFlag.show = false;
      this.$refs.inputFlag.showE = false;
      // this.show = false;
      // this.showE = false;
    },
    changeToBigWithCarNum(value) {
      value = value.toUpperCase();
      this.carNum = value;
    },
    changeToBigWithFdjNum(value) {
      value = value.toUpperCase();
      this.fdjNum = value;
    }
  }
};
</script>
